import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  Button,
  FormControl,
  FormLabel,
  Input,
  Select,
  VStack,
  useToast,
  useColorMode,
  InputGroup,
  InputLeftElement,
  InputRightElement,
  Text,
} from '@chakra-ui/react';
import { useState, useEffect } from 'react';
import { apiService, Email } from '../services/api';

interface TransactionFormProps {
  isOpen: boolean;
  onClose: () => void;
  transaction?: Email;
  onSuccess: () => void;
}

export function TransactionForm({ isOpen, onClose, transaction, onSuccess }: TransactionFormProps) {
  const { colorMode } = useColorMode();
  const toast = useToast();
  const [isSubmitting, setIsSubmitting] = useState(false);

  const categories = [
    'Ăn uống',
    'Di chuyển',
    'Mua sắm',
    'Hóa đơn & Tiện ích',
    'Giải trí',
    'Sức khỏe',
    'Giáo dục',
    'Khác',
  ];

  const [formData, setFormData] = useState({
    expense: '',
    price: '',
    category: categories[0],
    emailId: '',
    month: new Date().getMonth() + 1,
    note: '',
  });

  useEffect(() => {
    if (transaction) {
      const category = transaction.category && categories.includes(transaction.category) 
        ? transaction.category 
        : categories[0];

      setFormData({
        expense: transaction.expense || '',
        price: transaction.price?.toString() || '',
        category: category,
        emailId: transaction.emailId || '',
        month: transaction.month || new Date().getMonth() + 1,
        note: transaction.note || '',
      });
    } else {
      setFormData({
        expense: '',
        price: '',
        category: categories[0],
        emailId: '',
        month: new Date().getMonth() + 1,
        note: '',
      });
    }
  }, [transaction]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      const data = {
        ...formData,
        price: parseFloat(formData.price),
        emailId: formData.emailId || `TRANS_${Date.now()}`,
        category: formData.category,
      };

      if (transaction?.id) {
        await apiService.updateEmail(transaction.id, data);
        toast({
          title: 'Cập nhật giao dịch thành công',
          status: 'success',
          duration: 3000,
          isClosable: true,
        });
      } else {
        await apiService.createEmail(data);
        toast({
          title: 'Tạo giao dịch thành công',
          status: 'success',
          duration: 3000,
          isClosable: true,
        });
      }

      onSuccess?.();
      onClose();
    } catch (error) {
      console.error('Error submitting form:', error);
      toast({
        title: 'Lỗi',
        description: error instanceof Error ? error.message : 'Đã xảy ra lỗi',
        status: 'error',
        duration: 3000,
        isClosable: true,
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>
          {transaction ? 'Chỉnh sửa giao dịch' : 'Tạo giao dịch mới'}
        </ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          <VStack spacing={4} as="form" onSubmit={handleSubmit}>
            <FormControl isRequired>
              <FormLabel>Mô tả</FormLabel>
              <Input
                name="expense"
                value={formData.expense}
                onChange={handleChange}
                placeholder="Nhập mô tả giao dịch"
              />
            </FormControl>

            <FormControl isRequired>
              <FormLabel>Số tiền</FormLabel>
              <InputGroup>
                <InputLeftElement pointerEvents="none" color="gray.500">
                  ₫
                </InputLeftElement>
                <Input
                  name="price"
                  type="number"
                  step="any"
                  value={formData.price}
                  onChange={handleChange}
                  placeholder="0.00"
                  min="-999999999"
                  max="999999999"
                />
                <InputRightElement pointerEvents="none" color="gray.500">
                  VND
                </InputRightElement>
              </InputGroup>
              <Text fontSize="sm" color="gray.500" mt={1}>
                Số dương cho thu nhập, số âm cho chi tiêu
              </Text>
            </FormControl>

            <FormControl isRequired>
              <FormLabel>Phân loại</FormLabel>
              <Select
                name="category"
                value={formData.category}
                onChange={handleChange}
                bg={colorMode === 'light' ? 'white' : 'gray.700'}
              >
                {categories.map((category) => (
                  <option key={category} value={category}>
                    {category}
                  </option>
                ))}
              </Select>
            </FormControl>

            <ModalFooter px={0}>
              <Button variant="ghost" mr={3} onClick={onClose}>
                Hủy
              </Button>
              <Button
                colorScheme="blue"
                type="submit"
                isLoading={isSubmitting}
                loadingText="Đang xử lý..."
              >
                {transaction ? 'Cập nhật' : 'Tạo mới'}
              </Button>
            </ModalFooter>
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  );
} 